/*
 * Colors
 * ========================================================================== */
@import url("https://fonts.googleapis.com/css?family=Work+Sans");
.about-text {
  font-size: 2.5em; }

.about-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
